@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing:border-box;
  margin: 10;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  
  /* background-image:url(cryptoo.png); */
  color: #fff;
}

Coin{

}

.container {
  z-index: 1;
}

.navstyle {
  background-color: transparent;
  display : -webkit-flex;
  display : flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin-left: 30px;
  margin-top: 30px;
  color: #f0e9e9;
  font-family: Cinzel;
  font-style:italic ;
  font-size: small;
  z-index: 1;
}

body{
  background-color: #1a1a1c;
}

.modal-container{
  display: -webkit-flex;
  display: flex;
  height:60vh;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: bisque;
}

.circle {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 400px;
  height: 400px;
  background-color: rgba(251, 255, 251, 0.719);
  border-radius: 50%;
  opacity: 0.1;
  z-index: 1;
  position:fixed;
  -webkit-align-content: flex-end;
          align-content: flex-end;
  
}


.cirlce-right {
  display: -webkit-flex;
  display: flex;
  
}

.text {
  margin: auto;
  z-index: 1;
}

.coin-app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 64px;
  border-radius:20%;
  color: #fff;
  z-index: -2;
}

.coin-search {
  margin-bottom: 64px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
  z-index: 1;
  
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}


@media (max-width: 1014px) {

  .coin-text{
    font-size: 25px;
  }

  .coin-search{
    margin-top: 100px;
  }
}
.coin-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%
}

.coin-container:hover{
  background-color: #575454;
}


.scroll-to-top{
  position: fixed;
  bottom: 0;
  right:0;
  z-index:9;
  margin: 2rem;
}

.fa, .fas {
  font-size: xxx-large;
    padding-left: 10px;
    padding-top: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    background-color: transparent;
  }

.fas:hover{
  background-color: white;
  color: black;
  border-radius: 50%;
}



.coin-row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 1000px;
}

.coin {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-right: 24px;

  min-width: 300px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 110px;
}

.coin-volume {
  width: 155px;
}

.coin-marketcap {
  width: 230px;
}

.coin-percent {
  width: 100px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

@media (max-width: 1014px) {

  .coin-row{
    width:340px;
  }

  .coin h1{
    width:100px;
    font-size:13px;
  }

  .fa, .fas {
    font-size: xx-large;
    }

  *{
    font-size: 13px;
  }

  .coin {
    min-width: 140px;
    }

  div.coin-container:nth-child(2) > div:nth-child(1) {
    padding-left: 0px;
    margin-left: 9px;
    }
  
  div.coin-container:nth-child(2) {
    margin-right: 0px;
    margin-left: 0px;
    }


  .coin-container{
    -webkit-justify-content: center;
            justify-content: center;
  }


  .coin-volume{
    display: none;
  }

  .coin-marketcap{
    display:none;
  }

  .coin-symbol{
    display:none;
  }

}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    color: white;
    z-index: 10;
  }
  
.modal-main {
  position:fixed;
  background: #1a1a1a;
  width: 80%;
  
  height: auto;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  border: 1px #ac32e4 solid;
  border-radius: 10px;
  width: 500px;
  min-width: 300px;
}

@media (max-width: 510px) {
  .modal-main {
    width: auto;
  }
  
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.coin-modal-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 4rem;
  padding-top: 2rem;
}
@media (max-width: 510px) {
  .modal-main {
    width: auto;
  }
  .coin-modal-container {
    padding: 1.5rem auto 3rem auto;
  }
  
}

@media (max-width: 420px) {
  .coin-modal-container {
    padding: 1.5rem 1.2rem 3rem 1.2rem;
  }
  
}

.coin-image {
  margin-bottom: 1rem;
}


.coin-modal-container .coin-name {
  margin-bottom: 1rem;
  font-size: 25px;
}

.coin-modal-container .coin-current {
  font-size: 20px;
  margin-bottom: 1rem;
  text-align: center;
}

.center {
  text-align: center;
  width: auto;
  font-size: 20px;
  margin-bottom: 1rem;
}

.btn-div{ 
  display: -webkit-flex; 
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin: 10px;
  background-color: #1a1a1a;
  color:#1a1a1a
}

.close-button {
  background: #ac32e4;

  border: 1px #ac32e4 solid;
  font-weight: 400px;
  font-size: 16px;
  padding-right: 7px;
  padding-left: 7px;
  border-radius: 3px;
}
.coin-modal-symbol {
  text-transform: uppercase;
  font-size: 22px;
  margin-bottom: 1rem;
}
